.gridItem{ display: flex; align-items: center; padding-top: 0px! important; }
.gridContainer{ display: flex; align-items: center; padding-left: 20px! important; margin-top: 10px! important; }
.disField { background-color: #e0e0e0! important; color: #838eab}
@media (min-width: 768px) and (max-width: 920px){
    .gridCol{ margin-left: 30px! important;}
} 
@media (max-width: 768px) {
    .gridMobile {
        padding-top: 5px! important;
    }
    .gridItem{ padding-top: 10px! important;}
  }
@media (max-width: 375px){
    .btnContainer{
        flex-direction: column;
    }
    .btnContainer button{
        margin-top: 10px;
    }
}